@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body{
    background-color: #00081d;
    color: white;
    position: relative;
    width: 100%;
    min-height: 100vh;
}

.movie-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    width: 700px;
    margin: auto;
    padding: 30px 20px;
    background-color: #6A8DE6;
    border-radius: 25px;
    box-shadow: 10px 9px 20px 0px #91a1cc;
}

.movie-name{
    text-align: center;
}

.movie-item__wrapper{
    padding-bottom: 170px;
}

.preview{
    width: 100%;
    display: flex;
    position: relative;
}

.trailer{
    position: absolute;
    text-decoration: none;
    display: flex;
    align-items: center;
    bottom: 10px;
    left: 250px;
    right: 370px;
    border: 1px solid white;
    border-radius: 50%;
    padding: 5px 11px;
    color: red;
    cursor: pointer;
    background-color: white;
    transition: left 0.5s;
    overflow: hidden;
}

.trailer:hover{
    left: 185px;
    border-radius: 30px;
    padding: 5px 15px;
    gap: 5px;
}

.trailer:hover .test{
    display: block;
    opacity: 1;
    width: fit-content;
    height: fit-content;
}
.test{
    /* display: none; */
    overflow: hidden;
    opacity: 0;
    font-weight: bold;
    transition: all 0.5s;
}



.movie_error{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
}
.description{
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    margin-left: 30px;
    width: 100%;
}


.poster{
    border-radius: 15px;
}

.no-poster{
    width: 300px;
    height: 437px;
}
.rate{
    display: flex;
    gap: 15px;
    align-items: center;
}

.rate-number{
    font-size: 21px;
    font-weight: bold;
}
.genres-list{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    gap: 15px;
}


.genre{
    border: 1px solid #b2b7c1;
    border-radius: 25px;
    padding: 5px 10px;
}

.info{
    display: flex;
    gap: 20px;
}

.plot{
    text-align: justify;
    align-self: flex-start;
}

.writter{
    display: flex;
    align-self: flex-start;
    padding-left: 5px;
    flex-wrap: wrap;
}

.actors-header{
    margin-right: 10px;
}

.cast{
    align-self: flex-start;
    display: flex;
    flex-wrap: wrap;
}

.country{
    display: flex;
    align-self: flex-start;
    padding-left: 5px;
}


.btn{
    position: absolute;
    bottom: 0px;
    width: stretch;
    height: 50px;
    background-color: transparent;
    border: 1px solid #b2b7c1;
    border-radius: 15px;
    color: white;
    font-size: 20px;
    cursor: pointer;
    margin-top: 10px;
    transition: all 0.3s;
}
.btn:hover{
    background-color: #00081d;
    border: 1px solid #00081d;
}


/* Search-querry */

.search-querry{
    margin-bottom: 70px;
    width: 70%;
    margin: auto auto 70px auto;
    text-align: center;
}
.search-querry p{
    margin-bottom: 10px;
    font-size: 30px;
    font-weight: bold;
}

.search-querry-content{
    display: flex;
    align-items: center;
    gap: 15px;
}


.search-btn{
    background-color: transparent;
    border: 1px solid #6A8DE6;
    padding: 12px 45px;;
    border-radius: 50px;
    color: white;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.3s;
}

.search-btn:hover{
    background-color: #6A8DE6;
}

/* Movie-List */


.movie-list{
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    flex-wrap: wrap;
    gap: 100px;
    padding-bottom: 170px;
    max-width: 1000px;
    margin: auto;
}

.movie-object{
    color: white;
    display: flex;
    gap: 40px;
    background-color: #6A8DE6;
    border-radius: 25px;
    box-shadow: 10px 9px 20px 0px #91a1cc;
    padding: 30px 20px;
    transition: all 0.5s;
    width: 450px;
    cursor: pointer;
}

.movie-object:hover{
    scale: 1.2;
}

.movie-poster{
    width: 150px;
    height: 250px;
}

.movie-description{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.movie-header{
    font-size: 1.2rem;
}

.movie-info{
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.movie-countries{
    display: flex;
    gap: 7px;
    flex-wrap: wrap;
}

.movie-genres{
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    font-size: 14px;
    font-weight: bold;
}


.movie-fact{
    display: flex;
    gap: 15px;
}


/* Layout */


.header{
    background-color: #424579;
    height: 100px;
    margin-bottom: 100px;
}

.header-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.logo{
    font-size: 40px;
    font-weight: bold;
}

.logo  span{
    color: #bac7e8
}

.pagination{
    display: flex;
    gap: 15px;
}

.pagination > a {
    color: white;
    text-decoration: none;
}

.footer{
    background-color: #424579;
    height: 70px;
    width: 100%;
    position: absolute;
    bottom: 0px;
}

.footer-wrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
}

.social-media{
    display: flex;
    gap:15px;
}

/* wrapper */

.wrapper{
    max-width: 1440px;
    margin: auto;
}

/* MyMovies */ 

.myMovies_header{
    text-align: center;
    margin-bottom: 30px;
}

/* About */

.about{
    display: flex;
    flex-direction: column;
    font-size: 14px;
    gap: 35px;
}


/* Login */ 

.login_wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
}

.login{
    border: 1px solid #bac7e8;
    background-color: #bac7e8;
    border-radius: 10%;
    padding: 25px;
}

.login_header{
    text-align: center;
}

.login_form{
    display: flex;
    flex-direction: column;
    gap: 35px;
    margin: 35px;
    align-items: center;
}

.form_button{
    background-color: #6A8DE6;
    border: none;
    cursor: pointer;
    border-radius: 15px;
    padding: 10px;
    width: 60%;
    color: white;
    font-size: 1.2em;
}

.login_offer{
    text-align: center;
    font-size: 1.2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.login_offer a{
    color: #424579;
    cursor: pointer;
    font-size: 1.4rem;
    background-color: transparent;
    border: none;
    transition: all 0.3s;
}

.login_offer a:hover{
    scale: 1.3;
}

/* error */ 

.error{
    text-align: center;
}